@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap'); /* Ensure VT323 font is imported */

.App {
  text-align: center;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'VT323', monospace; /* Apply VT323 font globally */
}

.App-header {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.vt323-regular {
  font-family: 'VT323', monospace;
  font-size: 3em;
  color: black;
}

.input-box, .ant-btn {
  font-family: 'VT323', monospace;  /* Apply VT323 font to input and buttons */
}

.ant-input, .ant-btn-primary {
  background-color: white !important; /* Set background to white */
  color: black !important;  /* Set text color to black */
  border-color: black !important;  /* Set border color to black */
}

.ant-btn-primary:hover {
  background-color: black !important;  /* Invert colors on hover */
  color: white !important;
}

.ant-message {
  font-family: 'VT323', monospace !important; /* Apply custom font to Ant Design message */
  font-size: 18px; /* Adjust font size if necessary */
}

.ant-message-notice-content {
  background-color: white !important; /* Ensure background is white */
  color: black !important; /* Ensure text color is black */
}
